/* [Object] Modal
 * =============================== */
.modal {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    text-align: left;
    background: rgba(0, 0, 0, 0.646);
    transition: opacity .25s ease;
}

.modal__bg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    cursor: pointer;
}

.modal-state {
    display: none;
}

.modal-state:checked+.modal {
    opacity: 1;
    visibility: visible;
}

.modal-state:checked+.modal .modal__inner {
    top: 0;
}

.modal__inner {
    transition: top .25s ease;
    position: absolute;
    top: -20%;
    right: 0;
    bottom: 0;
    left: 0;
    width: 90%;
    margin: auto;
    overflow: auto;
    background: #fff;
    border-radius: 5px;
    padding: 1em 2em;
    height: 65%;
    box-sizing: border-box;

    h2 {
        font-size: 2rem;
    }

    img {
        margin-top: 2rem;
        width: 100%;
        max-height: 360px;
        object-fit: cover;
        object-position: center top;
        border-radius: 5px;
    }

    @include mq(600px) {
        height: 50%;
    }

    @include mq(tablet-sm) {
        width: 70%;
        height: 60%;
    }
}

.modal__close {
    position: absolute;
    right: 1em;
    top: 1em;
    width: 1.1em;
    height: 1.1em;
    cursor: pointer;
}

.modal__close:after,
.modal__close:before {
    content: '';
    position: absolute;
    width: 2px;
    height: 1.5em;
    background: #ccc;
    display: block;
    transform: rotate(45deg);
    left: 50%;
    margin: -3px 0 0 -1px;
    top: 0;
}

.modal__close:hover:after,
.modal__close:hover:before {
    background: #aaa;
}

.modal__close:before {
    transform: rotate(-45deg);
}

/* testimonial card 
 * =============================== */
.testimonial-card {
    margin: 3rem auto;
    max-width: 1000px;

    @include mq(tablet-sm) {
        label {
            display: flex;
            gap: 2rem;
        }
    }
}

.testimonial-card__image {

    @include mq(tablet-sm) {
        max-width: 360px;
        max-height: 180px;
        overflow: hidden;
    }

    img {
        width: 100%;
        height: 15rem;
        object-fit: cover;
        object-position: center top;

        @include mq(tablet-sm) {
            height: 100%;
        }
    }
}

.testimonial-card__content {
    margin-top: 1rem;

    @include mq(tablet-sm) {
        margin-top: 0;
    }
}