/* [Object] Modal
 * =============================== */
.modal {
	opacity: 0;
	visibility: hidden;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	text-align: left;
	background: rgba(0, 0, 0, 0.646);
	transition: opacity 0.25s ease;
}

.modal__bg {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	cursor: pointer;
}

.modal-state {
	display: none;
}

.modal-state:checked + .modal {
	opacity: 1;
	visibility: visible;
}

.modal-state:checked + .modal .modal__inner {
	top: 0;
}

.modal__inner {
	transition: top 0.25s ease;
	position: absolute;
	top: -20%;
	right: 0;
	bottom: 0;
	left: 0;
	width: 90%;
	margin: auto;
	overflow: auto;
	background: #fff;
	border-radius: 5px;
	padding: 1em 2em;
	height: 65%;
	box-sizing: border-box;
}
.modal__inner h2 {
	font-size: 2rem;
}
.modal__inner img {
	margin-top: 2rem;
	width: 100%;
	max-height: 360px;
	object-fit: cover;
	object-position: center top;
	border-radius: 5px;
}
@media (min-width: 48em) {
	.modal__inner {
		width: 70%;
		height: 60%;
	}
}

.modal__close {
	position: absolute;
	right: 1em;
	top: 1em;
	width: 1.1em;
	height: 1.1em;
	cursor: pointer;
}

.modal__close:after,
.modal__close:before {
	content: "";
	position: absolute;
	width: 2px;
	height: 1.5em;
	background: #ccc;
	display: block;
	transform: rotate(45deg);
	left: 50%;
	margin: -3px 0 0 -1px;
	top: 0;
}

.modal__close:hover:after,
.modal__close:hover:before {
	background: #aaa;
}

.modal__close:before {
	transform: rotate(-45deg);
}

